@import url(https://fonts.googleapis.com/css?family=Lato:300,400);
@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Overpass+Mono);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --ecma-gold: #957E51; 
  --ecma-dark-gold: rgb(112, 93, 54);
  --ecma-light-gold: rgb(199, 176, 131);
  --ecma-blue: #192835;
}

body, html {
  margin:0;
}

a {
  text-decoration: none;
  color: #192835;
  color: var(--ecma-blue);
}

a:hover {
  color: #957E51;
  color: var(--ecma-gold);
}

.App {
  text-align: center;
}

.title {
  font-size: 3em;
  margin-bottom: 60px;
}

.section-subtitle {
  font-size: 1.3em;
  line-height: 1.6em;
}

.section-ma {
  margin-bottom: 10vh;
}

.section-container {
  padding-bottom: 60px;
}

.section-vpa {
  padding-bottom: 120px;
  padding-top: 120px;
}

.container-vpa {
  padding-bottom: 60px;
  padding-top: 60px;
}

.container-tpa {
  padding-top: 60px;
}

.center {
  display: flex;
  justify-content: center;
}

.top-page {
  margin-top: 12vh;
}

.start {
  display: flex;
  justify-content: flex-start;
}

.hpa {
  padding-left: 30px;
  padding-right: 30px;
}

.hma {
  margin-left: 30px;
  margin-right: 30px;
}
.min-max-width {
  max-width: 1080px;
  min-width: 360px;
}

.roboto {
  font-family: 'Roboto', 'Haleway', serif; 
}

.lato, .text {
  font-family: 'Lato', 'Haleway', serif; 
}

.text {
  line-height: 1.8em;
  font-size: 1.1em;
}

.overpass-mono {
  font-family: 'Overpass Mono', monospace;
}

.color-ecma-gold {
  color: #957E51;
  color: var(--ecma-gold);
}

.color-ecma-blue {
  color: #192835;
  color: var(--ecma-blue);
}

.bg-ecma-blue {
  background-color: #192835;
  background-color: var(--ecma-blue);
}

.color-default {
  color: #413E3E;
}

.button {
  display: inline-block;
  max-width: 16em;
  padding: 0.35em 1.2em;
  border-radius: 2em;
  background-color: #957E51;
  background-color: var(--ecma-gold);
  box-sizing: border-box;
  font-weight: 300;
  font-size: 20px;
  line-height: 25px;
  text-align: center;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  margin-top: 1em;
  cursor: pointer;
}

.button:hover {
  background-color: rgb(112, 93, 54);
  background-color: var(--ecma-dark-gold);
}

.disabled, .disabled:hover, .disabled:active  {
  background-color: rgb(199, 176, 131);
  background-color: var(--ecma-light-gold);
  cursor: auto;
}

#map {
  height: 40vh;
  width: 100%;
}
.navigation {
    padding: 1vh 2vh 1vh 2vh;
    position: fixed;
    top: 0;
    background-color: white;
    z-index: 1;
    width: 100%;
}

.mobile-navigation {
    box-shadow: 0 4px 2px -2px rgba( 0, 0, 0, 0.2 );
    background-color: white;
    position: fixed;
    z-index: 1;
    width: 100%;
    display: none;
}

.logoContainer {
    float: left;
}

.headerLogo {
    height: 10vh;
}

.menuItems {
    height: 10vh;
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-end;
    align-items: center;
    text-align: center;
}

.menuItems span {
    padding-left: 40px;
}

.menuItems p, .menu-mobile p {
    font-size: 20px;
    cursor: pointer;
    color: #192835;
}

.menu-mobile p {
    font-size: 20px;
}

.dropdown-content p {
    font-size: 16px;
}

.menuItems p:hover, .menu-icon:hover, .menu-mobile p:hover {
    color: #957E51;
}

.menu-icon {
    display: none;
}

.dropdown:hover .dropdown-content{
    display: block;
}

.dropdown-content {
    display: none;
    position: absolute;
    background-color: white;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}

.dropdown-content p {
    float: none;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    text-align: left;
    cursor: pointer;
}

.dropdown-content p:hover {
    color: var(--ecma-gold);
}

.menu-mobile {
    padding: 15px 0 15px 0;
}

@media screen and (max-width: 900px) {
    .menuItems span {
        display: none;
    }

    .menu-icon,  .show-mobile-menu, .show-mobile-menu .dropdown {
        display: block;
    }
    
    .show-mobile-menu .dropdown {
        width: 100%;
    }

    .dropdown-content {
        position: relative;
        box-shadow: none;
    }
}
.footer-logo {
    max-width: 300px;
}

.footer-icon {
    margin-right: 25px;
}

.footer-text {
    font-size: 1em;
    line-height: 1.4em;
}

.footer-inner {
    display: flex;
    justify-content: center;
    flex-direction: row;
    flex-grow: 1;
}

.footer-social-media {
    flex-direction: column;
}

.footer-link:visited, .footer-link:link {
    color: white;
}

.footer-link:hover {
    color: lightgrey;
}

.footer-link:active {
    color: grey;
}

.social-icon {
    margin: 5px 0px 5px 0px;
}

@media screen and (max-width: 800px) {
    .footer-inner {
        flex-direction: column;
    }

    .footer-info {
        margin-top: 30px;
        flex-direction: column;
    }

    .footer-social-media {
        flex-direction: row;
        margin-top: 30px;
    }

    .social-icon {
        margin: 0px 5px 0px 5px;
    }
}
form input, form textarea, .input-mask {
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    margin-top: 6px;
    margin-bottom: 16px;
    resize: vertical;
}

form {
    max-width: 900px;
    min-width: 200px;
}

form textarea {
    height: 200px;
}

form label {
    color: red;
}

.form-error {
    border-color: red;
}

.success-message {
    color: green;
    
}

.form-button {
    width: 6em;
}
.contact-info {
    display: grid;
    grid-template-columns: 40px auto;
    grid-template-rows: auto-fit auto-fit auto-fit;
    align-items: center;
    justify-content: center;
    grid-row-gap: 10px;
    grid-column-gap: 5px;
}

.contact-subtitle {
    font-size: 1.3em;
    margin-bottom: 60px;
    margin-top: 60px;
}

#icon1 {
    grid-row-start: 1;
    grid-column-start: 1;
}

#icon2 {
    grid-row-start: 2;
    grid-column-start: 1;
}

#icon3 {
    grid-row-start: 3;
    grid-column-start: 1;
}

#text1 {
    grid-row-start: 1;
    grid-column-start: 2;
}

#text2 {
    grid-row-start: 2;
    grid-column-start: 2;
}

#text3 {
    grid-row-start: 3;
    grid-column-start: 2;
}
.card-container {
    width: 100%;
    padding: 20px;
    padding-top: 4em;
    padding-bottom: 4em;
}

.card-container-bg {
    background-color: hsl(0, 0%, 97%);
}

.card-img {
    width: 45%;
    height: 300px;
}

.card-info {
    width: 55%;
    padding: 0 2em; 
}

.card-flip {
    flex-direction: row-reverse;
}

@media screen and (max-width: 900px) {
    .card-container {
        justify-content: center;
    }

    .card-inner {
        flex-direction: column;
        max-width: 500px;
    }

    .card-img {
        width: 100%;
        height: 300px;
    }

    .card-info {
        width: 100%;
        padding: 0;
    }

    .card-button {
        float: none;
        width: 100%;
    }
}

@media screen and (max-width: 600px) {
    .card-inner {
        max-width: 300px;
    }

    .card-img {
        height: 200px;
    }
}
.service-title {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    background-color: rgba(0, 0, 0, 0.6);
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-blend-mode: overlay;
}

.other-services p {
    font-size: 3em;
}

.other-img {
    background-size: 250px 250px;
    background-position: center;
    height: 250px;
    width: 250px;
    -webkit-clip-path: polygon(0 50%, 50% 100%, 100% 50%, 50% 0);
            clip-path: polygon(0 50%, 50% 100%, 100% 50%, 50% 0);
    margin: 20px;
}

.service-column-1 {
    width: 60%;
}

.service-column-2 {
    width: 40%;
}

@media screen and (max-width: 900px) {
    .service-column-1,  .service-column-2 {
        width: 100%;
    }
}
.member-container {
    margin-bottom: 10px;
    height: 500px;
} 

.member-photo {
    width: 50%;
    background-color: lightgray;
}

.member-info {
    width: 50%;
    flex-grow: 1;
}

.member-info-1 {
    background-color: var(--ecma-blue);
    color: rgb(236, 236, 236);
    line-height: 1.5em;
    font-size: 0.9em;
}

.member-info-2 {
    font-size: 0.9em;
}

.member-info-1, .member-info-2 {
    width: 50%;
}

.member-info-1, .member-info-2, .member-info-3 {
    height: 50%;
    padding: 1.5em;
}

.member-info-3 {
    background-color: rgb(219, 219, 219);
    width: 100%;
}

@media screen and (max-width: 960px) {
    .member-container {
        flex-direction: column;
        height: 800px;
    }

    .member-photo {
        width: 100%;
        height: 50%;
    }

    .member-info {
        width: 100%;
        height: 50%;
        flex-direction: row;
    }

    .member-info-1, .member-info-2 {
        width: 50%;
    }
}

@media screen and (max-width: 500px) {
    .member-info-1, .member-info-2 {
        padding: 1em;
    }

    .member-info-1 {
        width: 40%;
        font-size: 0.8em;
    }

    .member-info-2 {
        font-size: 0.8em;
        width: 60%
    }
}
.clinic-panel {
    width: 55%;
    height: auto;
    padding: 1em 2em 1em 2em;
    margin-top: 1em;
    margin-bottom: 1em;
}

.clinic-img {
    width: 45%;
    background-color: lightgray;
}

@media screen and (max-width: 900px) {
    .clinic-panel {
        width: 100%;
        height: auto;
    }

    .clinic-img {
        height: 20em;
    }

}
.icon-card {
    width: 325px;
    padding: 2.5em;
}

.icon-card-text {
    height: 4.5em;
    line-height: 1.5em;
}

@media screen and (max-width: 750px) {
    .icon-card {
        width: 100%;
    }
}
@media screen and (max-width: 600px) {
    .faith-section {
        align-items: center;
    }
}
.slide-bts {
    margin-top: 125px;
    margin-bottom: 30px;
}

.slide-button {
    width: 20px;
    height: 20px;
    border: 2px solid #957E51;
    border-radius: 100%;
    background-color: #192835;
}
.carousel {
    width: 100%;
    height: auto;
    overflow: hidden;
}

.carousel-container {
    background-image: url(/ecma-site/static/media/clinica.f33ab4cb.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-color: rgba(25, 40, 53, 0.61);
    width: 100%;
    height: 100%;
    background-blend-mode: overlay;
    overflow: hidden;

}

.carousel-title {
    font-size: 4em;
}

.carousel-subtitle {
    font-weight: 300;
    font-size: 3vh;
}

@media screen and (max-width: 700px) {
    .carousel-title {
        font-size: 10vw;

    }
    .carousel-subtitle {
        font-size: 7vw;
    }
}


.intro-title {
    text-align: start;
    margin-top: 0px;
    margin-bottom: 0px;
    font-size: 5em;
}

.intro-text-container {
    width: 40%;
}

.intro-text {
    font-size: 1em;
}

@media screen and (max-width: 900px) {
    .intro-text-container {
        width: 100%;
    }
}

@media screen and (max-width: 600px) {
    .intro-title {
        font-size: 12vw;
    }
}

@media screen and (max-width: 460px) {
    .intro-title {
        font-size: 12vw;
    }
}
.services-container {
    background-color: #EAE6E6;
}

.services-title {
    color: rgba(61, 86, 107, 0.6);
}

.services-bt {
    width: 250px;
    height: 250px;
    border: 0px;
    border-radius: 100%;
    margin: 30px;
    background-color: rgba(0, 0, 0, 0.35);
    background-position: center;
    background-blend-mode: overlay;
    font-size: 1.8em;
    cursor: pointer;
}

#pilates-bt {
    background-image: url(/ecma-site/static/media/pilates.1289a125.jpg);
    background-size: 450px 300px;
}

#treinamento-bt {
    background-image: url(/ecma-site/static/media/treinamento.15c76ae1.jpg);
    background-size: 300px 400px;
}

#fisioterapia-bt {
    background-image: url(/ecma-site/static/media/fisioterapia.7fc7dc80.jpg);
    background-size: 450px 300px;
}

#osteopatia-bt {
    background-image: url(/ecma-site/static/media/osteopatia.39752863.jpg);
    background-size: 614px 300px;
}
.testmony {
    width: 30%;
}

@media screen and (max-width: 900px) {
    .testmony {
        width: 100%;
    }
}
